import React from 'react';
import './App.css';
import EventListener from './EventListener';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      youtubeurl: "blb3X5zpdpU",
      time: 102,
      replayNum: 50,
      replayDuration: 4,
      caption: true,
      speed: 1,
      timerRemain: 0,
      timerRemainTime: 0
    }
    this.onChangeUrl = this.onChangeUrl.bind(this);
    this.onChangeTime = this.onChangeTime.bind(this);
    this.onChangeReplayNum = this.onChangeReplayNum.bind(this);
    this.onChangeReplayDuration = this.onChangeReplayDuration.bind(this);

    this.onReplay = this.onReplay.bind(this);
    this.onReplay5 = this.onReplay5.bind(this);
    this.setTextInputRef = element => {
      this.textInput = element;
    };
    this.initialFocus = () => {
      // Focus the text input using the raw DOM API
      if (this.textInput) this.textInput.focus();
      this.textInput.blur();
      window.scrollTo(0, 0);
    };
    this.timerList = []
    this.sourceText=null
    this.lastTransFocus=null
  }
  componentDidMount() {
    let urlParamStr = window.location.search
    if (urlParamStr) {
      //?を除去
      urlParamStr = urlParamStr.substring(1)

      let params = {}

      //urlパラメータをオブジェクトにまとめる
      urlParamStr.split('&').forEach(param => {
        const temp = param.split('=')
        //pramsオブジェクトにパラメータを追加
        params = {
          ...params,
          [temp[0]]: temp[1]
        }
      })
      if (params.reactyoutube) {
        let videoId = params.reactyoutube.replace(/.*%3D/g, '');
        this.setState({ youtubeurl: videoId })
      }
    }
    setInterval(() => {
      if (this.state.timerRemain > 0) {
        let remain = this.state.time + this.state.replayDuration - window['XXXXX'].getCurrentTime()
        let tremain = Math.trunc(remain * 10) / 10
        this.setState({ timerRemainTime: tremain })
      }
      this.textFoucsCheck()
    }, 200);
    setInterval(() => {
      let el = document.activeElement
      if (el.id === "reactyoutube" && [1].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
        this.initialFocus()
      }
    }, 3000)
    let seekTime = localStorage.getItem('react-youtube-seekTime')
    if (seekTime) {
      this.setState({ time: parseFloat(seekTime) })
    }
  }
  componentWillUnmount() {
  }
  textFoucsCheck(){
    //3秒超えたらfocusを外す
    let el=document.activeElement
    if(el!=null && el.id=="source"){
      if(this.lastTransFocus==null){
        this.lastTransFocus=new Date()
        this.sourceText=el.value
      }else if(this.sourceText!=el.value){
        this.lastTransFocus=new Date()
        this.sourceText=el.value
      }else if(new Date().getTime()-this.lastTransFocus.getTime()>3000){
        this.initialFocus()
        this.lastTransFocus=null
        this.sourceText=null
      }
    }else{
      this.lastTransFocus=null
      this.sourceText=null
    }
  }
  onChangeUrl(e) {
    const newLocal = e.target.value;
    const replace = newLocal.replace(/.*=/g, '');
    this.setState({ youtubeurl: replace, time: 0 }, () => {
      window['XXXXX'].loadVideoById(this.state.youtubeurl, this.state.time);
      this.initialFocus()
    })
  }
  onChangeTime(e) {
    this.setTimeState(parseFloat(e.target.value))
  }

  setTimeState(sec) {
    this.setState({ time: sec });
    localStorage.setItem('react-youtube-seekTime', sec)
  }
  onChangeReplayNum(e) {
    this.setState({ replayNum: parseInt(e.target.value) });
  }
  onChangeReplayDuration(e) {
    this.setState({ replayDuration: parseFloat(e.target.value) });
  }

  onReplay(e) {
    window['XXXXX'].seekTo(this.state.time, true);
    // if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
    //   console.log("playvideo");
    //   window['XXXXX'].playVideo();
    // }
  }
  onReplay5(e) {
    this.clearAllTimer()
    if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
      this.setTimeState(window['XXXXX'].getCurrentTime());
      console.log("playvideo");
      window['XXXXX'].playVideo();
    } else {
      this.onReplay()
    }
    this.setState({ timerRemain: this.state.replayNum - 1 })
    this.onReplay5SetInterval(this.state.replayNum - 1);
  }
  onReplay5SetInterval(remainNum) {
    let num = 0;
    this.timerList.push(
      setInterval(() => {
        if (this.timerList.size === 0) {
          //キャンセルされたので何もしない
          return
        }
        console.log("onReplay5SetInterval")
        const newLocal = this.state.time + this.state.replayDuration;
        if (window['XXXXX'].getCurrentTime() > newLocal) {
          console.log("clearInterval")
          this.clearAllTimer()
          this.setState({ timerRemain: remainNum })
          if (remainNum > 0) {
            this.onReplay();
            this.onReplay5SetInterval(remainNum - 1);
          }
        }
        //0.1*2000回=200秒間タイマーが続いたら削除
        if (num++ > 2000) {
          console.log("clearInterval exceed")
          this.clearAllTimer()
        }
      }, 200)
    )
  }
  clearAllTimer() {
    this.timerList.concat().forEach(t => {
      clearInterval(t)
    })
    this.timerList = []
  }
  typeOf(obj) {
    return toString.call(obj).slice(8, -1).toLowerCase();
  }
  keyProc(key, event) {
    console.log(key + " pressed")
    console.log(Object.prototype.toString.call(event.target))
    console.log(event.target.nodeType)
    console.log(this.typeOf(event.target))

    if (key === "Escape") {
      event.target.blur();
    }
    if (this.typeOf(event.target) !== "htmlbodyelement") {
      console.log("bodyではないためkeyevent無視");
      return;
    }
    if (key === "k") {
      console.log("state:" + window['XXXXX'].getPlayerState())
      //https://developers.google.com/youtube/iframe_api_reference#Playback_status
      if ([-1, 0, 2, 5].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
        console.log("playvideo");
        window['XXXXX'].playVideo();
      }
      if ([1].indexOf(window['XXXXX'].getPlayerState()) >= 0) {
        window['XXXXX'].pauseVideo();
        this.clearAllTimer()
        this.setState({ timerRemain: 0, timerRemainTime: 0 })
      }
    }
    if (key === "c") {
      this.setState({ caption: !this.state.caption });
    }
    if (key === "s") {
      if (this.state.speed === 1) {
        window['XXXXX'].setPlaybackRate(0.25)
        this.setState({ speed: 0.25 });
      } else {
        window['XXXXX'].setPlaybackRate(1)
        this.setState({ speed: 1 });
      }
    }
    if (key === "r") {
      this.clearAllTimer()
      this.setState({ timerRemain: 0, timerRemainTime: 0 })
      this.onReplay();
    }
    if (key === "l") {
      this.clearAllTimer()
      this.onReplay5();
    }
    if (key === "t") {
      this.setTimeState(window['XXXXX'].getCurrentTime());
    }
    if (key === "h") {
      window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 0.5), true);
    }
    if (key === "j" || key === "ArrowLeft") {
      window['XXXXX'].seekTo((window['XXXXX'].getCurrentTime() - 2), true);
    }
    if (key === "i") {
      document.querySelector('#source').value = ""
      document.querySelector('#source').focus()
      event.stopPropagation()
      event.preventDefault();
    }
    if (key === "p") {
      window.open('https://www.google.co.jp/search?&q='+document.querySelector('#source').value,'_blank')
    }
  }


  render() {
    return (
      <div className="App">
        <EventListener
          eventTarget={document}
          keydown={(event) => this.keyProc(event.key, event)}
        />
        <div id="hidecap" style={{
          position: "absolute", top: "390px", left: "0px", backgroundColor: "#CCCCFF",
          width: "700px",
          height: "120px",
          display: (this.state.caption ? "none" : "")
        }}></div>
        <div style={{
          display: "grid",
          gridTemplateColumns: "450px 80px 100px"
        }}>
          <div >
            <input type="text" name="youtubeurl" size="20" ref={this.setTextInputRef} value={this.state.youtubeurl} onChange={this.onChangeUrl}></input>
            <input type="number" name="time" size="20" value={this.state.time} onChange={this.onChangeTime}></input>
            <input type="number" name="replayNum" size="20" value={this.state.replayNum} onChange={this.onChangeReplayNum}></input>
            <input type="number" name="replayDuration" size="20" value={this.state.replayDuration} onChange={this.onChangeReplayDuration}></input>
            {/* <button type="button" onClick={this.onReplay}>replay</button> */}
            {/* <button type="button" onClick={this.onReplay5}>replayNum</button> */}
          </div>
          <label style={{ display: "block" }}>{"timer残" + this.state.timerRemain}</label>
          <label style={{ display: "block" }}>{"時間" + ((x) => {
            if (x > 1) {
              return Math.trunc(x)
            }
            if (Math.trunc(x) === x) {
              return "" + x + ".0";
            } else {
              return x;
            }
          })(this.state.timerRemainTime)
          }</label>
        </div>
      </div >
    )
  }
}

export default App;
