import React from 'react';
import PropTypes from 'prop-types';

export default class EventListener extends React.Component {
  static propTypes = {
    eventTarget: PropTypes.object.isRequired,
  };

  componentDidMount() {
    // イベントの設定
    this.getListeners().forEach( ( [ type, listener ] ) => {
      this.props.eventTarget.addEventListener( type, listener );
    } );
  }

  componentWillUnmount() {
    // イベントの設定解除
    this.getListeners().forEach( ( [ type, listener ] ) => {
      this.props.eventTarget.removeEventListener( type, listener );
    } );
  }

  getListeners() {
    // propsから、イベント名と関数を抽出
    let o= Object
      .keys( this.props )
      .filter( key => (key.startsWith( 'on' ) || key==="keydown" ))
			.map( key => {
				if(key.startsWith( 'on' )){
					return [ key.slice( 2 ).toLowerCase(), this.props[key] ];
				}else{
					return [ key, this.props[key] ];
				}
			});
			return o;
  }

  render() {
    return null; // 何も描画しない
  }
}